@import url("https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,300..900;1,300..900&display=swap");
/** Media Queries **/
.navbar {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 0;
  justify-content: space-between;
  align-items: center;
  background-color: #fff;
  height: 9rem;
  gap: 3rem;
  padding: 0 2rem;
}
@media (min-width: 768px) {
  .navbar {
    position: absolute;
    left: 0;
    right: 0;
    z-index: 99999999;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 0 5rem;
    width: 90%;
    height: 8rem;
    border-radius: 10rem;
    background-color: rgb(42.6611570248, 61.7851239669, 27.2148760331);
    margin: 2rem auto;
  }
}
.navbar__logo {
  width: 7rem;
}
.navbar__logo img {
  width: 100%;
}
.navbar__menu {
  display: none;
  flex-direction: column;
  text-align: center;
  gap: 2rem;
  padding: 0;
  font-weight: normal;
  color: #0f1011;
}
.navbar__menu a {
  transition: color 0.3s ease-in-out;
}
@media (min-width: 1024px) {
  .navbar__menu a {
    color: #fff;
  }
}
.navbar__menu a:hover {
  color: #74A84A;
}
.navbar__menu.show {
  position: fixed;
  display: flex;
  flex-direction: column;
  height: 100%;
  left: 0;
  top: 0;
  padding: 10rem 4rem 1rem 4rem;
  background-color: #fff;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.231372549);
  margin: 0;
  z-index: 99;
}
@media (min-width: 1024px) {
  .navbar__menu {
    position: initial;
    transform: translateX(0%);
    display: flex;
    flex-direction: row;
  }
}
.navbar.fixed {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 99;
  box-shadow: 3px 3px 15px rgba(0, 0, 0, 0.231372549);
  margin: 0 auto;
  animation-name: slide-in;
  animation-duration: 0.5s;
}
@media (min-width: 1024px) {
  .navbar.fixed {
    width: 90%;
    margin-top: 2rem;
  }
  @keyframes slide-in {
    from {
      transform: translateY(-100%);
    }
    to {
      transform: translateY(0);
    }
  }
}

.mobile-button {
  display: block;
  padding: 1.5rem !important;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.231372549);
  background-color: #74A84A;
  padding: 1.5rem 4rem;
  color: #fff;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  border-radius: 0.3rem;
}
.mobile-button:hover {
  background-color: rgb(168.5702479339, 204.2396694215, 139.7603305785);
  cursor: pointer;
}
@media (min-width: 768px) {
  .mobile-button {
