.header {
  position: relative;
  height: 40rem;
  width: 100%;
  /*background-image: url('../img/header_bg.webp');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;*/
  padding-top: 10rem;
  font-family: "Inter", sans-serif;
  overflow: hidden;
}
@media (min-width: 768px) {
  .header {
    height: 60rem;
  }
}
@media (min-width: 1024px) {
  .header {
    height: 80rem;
  }
}
.header__content {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  z-index: 2;
  margin-top: 5rem;
}
.header__subtitle, .header__title, .header__button {
  margin-top: 0;
  text-align: center;
  text-transform: uppercase;
}
.header__subtitle {
  color: #fff;
  font-size: clamp(2.5rem, 6vw, 7rem);
  font-weight: lighter;
  margin-bottom: 0;
  text-shadow: 3px 3px 5px rgb(7.5295275591, 7.5295275591, 7.5295275591);
}
.header__title {
  font-weight: bold;
  font-size: clamp(4rem, 10vw, 15rem);
  color: #74A84A;
  text-shadow: 3px 3px 5px rgb(7.5295275591, 7.5295275591, 7.5295275591);
  margin-bottom: 2rem;
}
.header__button {
  background-color: #74A84A;
  color: #fff;
  font-size: clamp(2rem, 3vw, 3.5rem);
  width: auto;
  margin: 0 auto;
  padding: 1rem 2rem;
  transition: all 0.3s ease-in-out;
  border-radius: 0.3rem;
}
.header__button:hover {
  background-color: rgb(123.4289256198, 177.7280991736, 79.5719008264);
  transform: scale(105%);
}
.header .header__background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.header .header__background img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.header__layout {
  position: absolute;
  background: #0d0d0d;
  background: linear-gradient(0deg, rgba(13, 13, 13, 0.6) 5%, rgba(237, 221, 83, 0) 80%);
  width: 100%;
  height: 100%;
  display: block;
  z-index: 2;
  top: 0;
  left: 0;
}

.featured-cards {
  display: none;
  justify-content: space-evenly;
  position: absolute;
  width: 100%;
  top: -15rem;
}
@media (min-width: 768px) {
  .featured-cards {
    display: none;
  }
}
.featured-cards__body {
  display: none;
  flex-direction: column;
  align-items: center;
  justify-content: start;
}
.featured-cards__body h3, .featured-cards__body p {
  color: #fff;
}
.featured-cards__body h3 {
  font-size: 1.2rem;
  width: 70%;
}
.featured-cards__body p {
  font-size: 1rem;
  width: 50%;
}
.featured-cards__card {
  position: relative;
  width: 30rem;
  height: 30rem;
  background: transparent;
  overflow: hidden;
  box-shadow: inset 20px 20px 20px rgba(0, 0, 0, 0.507), 5px 25px 20px rgba(0, 0, 0, 0.2), 5px 20px 30px rgba(0, 0, 0, 0.2), inset -20px -20px 25px rgba(255, 255, 255, 0.432);
  border-radius: 61% 39% 52% 48%/44% 59% 41% 56%;
  transition: 0.5s ease-in-out;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.featured-cards__card span {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.089);
  filter: blur(15px);
}
.featured-cards__card:hover {
  border-radius: 50%;
}
.featured-cards__card::before {
  content: "";
  position: absolute;
  top: 50px;
  left: 65px;
  width: 35px;
  height: 35px;
  background: #fff;
  border-radius: 50%;
  opacity: 0.9;
  z-index: 3;
}
.featured-cards__card::after {
  content: "";
  position: absolute;
  top: 90px;
  left: 85px;
  width: 15px;
  height: 15px;
  background: #fff;
  border-radius: 50%;
  opacity: 0.9;
  z-index: 3;
}
.featured-cards__card img {
  position: absolute;
  width: 100%;
  object-fit: cover;
  height: 100%;
  z-index: -1;
}

