  border-top: 1px solid #333333;
  align-items: center;
}

*, *:before, *:after {
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
  background-color: #fff;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Rubik", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
  background-color: #fff;
}
a {
  color: #0f1011;
  text-decoration: none;
}

img {
  width: 100%;
  display: block;
}

.btn {
  text-align: center;
  padding: 0.8rem;
  background-color: #74A84A;
  color: #fff;
  border-radius: 0.8rem;
  font-weight: bold;
  transition: all 0.3s ease-in-out;
  border: none;
}
.btn:hover {
  background-color: rgb(128.7396694215, 180.847107438, 86.652892562);
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

.main-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
}

.wrapper {
  position: relative;
  overflow: hidden;
}

.bodySpan {
  background-color: rgba(0, 0, 0, 0.329);
  display: block;
  height: 100%;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 40;
}

.mt-10 {
  margin-top: 2.5rem;
}

#developed {
  font-size: 1.4rem;
  color: #9d999c;
  text-align: center;
  margin: 0;
}
#developed a {
  color: #6C45F3;
}

