@use 'base/variables' as v;
@use 'sass:color';


/** Media Queries **/
@mixin telefono {
    @media (min-width: v.$telefono) {
        @content;
    }
}
@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}
@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}


@mixin boton($bgColor, $colorTexto) {
    background-color: $bgColor;
    padding: 1.5rem 4rem;
    color: $colorTexto;
    font-size: 1.6rem;
    display: inline-block;
    font-weight: 700;
    border: none;
    transition-property: background-color;
    transition-duration: .3s;
    text-align: center;
    border-radius: .3rem;
    
    &:hover {
        background-color: color.adjust($bgColor, $lightness: 20%);
        cursor: pointer;
    }

    @include tablet {
        width: auto;
    }
}

@mixin grid($columnas, $espaciado ) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}

@mixin textoDegradado($color){
    background: linear-gradient(to right, $color 0%, color.adjust($color, $lightness: 50%));
    color: transparent;
    background-clip:text;
    font-size: 7rem;
    text-align: center;
    @include tablet{
        font-size: 10rem;

    }
}

@mixin imageCard($w, $h){
    width: $w;
    height: $h;
    border-radius: 1rem;
    overflow: hidden;
}